import "../styles/index.scss";
import langList from "./lang";
import initMenu from "./initMenu";
var buttons = document.querySelectorAll(".language-switcher__btn");
var textElements = document.querySelectorAll("[data-translate-id]");
var lng = "eng";
var changeLanguage = function () {
    textElements.forEach(function (textEl) {
        var key = textEl.dataset.translateId;
        var text = langList[key][lng];
        textEl.innerHTML = text;
    });
};
buttons.forEach(function (btn) {
    btn.addEventListener("click", function () {
        var newLng = btn.dataset.lng;
        if (newLng === lng)
            return;
        lng = newLng;
        changeLanguage();
    });
});
changeLanguage();
initMenu();
